import React, { useState } from "react";
import { useGetAdvertsQuery } from "../../redux/adverts/advertsSlice";
import Loader from "../../components/Loader/Loader";
import {
  Type,
  Description,
  AdvertsList,
  AdvertItem,
  AdvertImg,
  CarDetails,
  MakeModelYear,
  RentalPrice,
} from "./Catalog.styled";
import LoadMoreBtn from "../../components/Buttons/LoadMoreBtn/LoadMoreBtn";
import LearnMoreBtn from "../../components/Buttons/LearnMoreBtn/LearnMoreBtn";

const Catalog = () => {
  const [page, setPage] = useState(1);
  const {
    data: adverts,
    error,
    isLoading,
    isFetching,
  } = useGetAdvertsQuery(page);

  const handleLoadMore = () => {
    setPage((prevPage) => prevPage + 1);
  };

  const hasMoreAdverts = adverts ? adverts.length > 0 : false;
  const itemsPerPage = 12;

  const isLastPage =
    !isFetching && (!hasMoreAdverts || adverts.length < itemsPerPage);

  return (
    <>
      {error && <p>Ooops... something went wrong</p>}
      {isLoading ? (
        <Loader />
      ) : (
        <>
          <AdvertsList>
            {adverts.map((advert) => (
              <AdvertItem key={advert.id}>
                <AdvertImg src={advert.img} alt="Car" />
                <>
                  <Type>
                    <MakeModelYear>
                      {advert.make} {advert.model}, {advert.year}
                    </MakeModelYear>{" "}
                    <RentalPrice>{advert.rentalPrice}</RentalPrice>
                  </Type>
                  <Description>
                    {advert.address} | {advert.rentalCompany}
                  </Description>
                  <CarDetails>
                    {advert.type} | {advert.model} | {advert.id} |{" "}
                    {advert.functionalities[0]}
                  </CarDetails>
                </>
                <LearnMoreBtn
                  // onClick={handleLearnMore}
                  // disabled={isFetching}
                  text="Learn more"
                />
              </AdvertItem>
            ))}
          </AdvertsList>
          {isFetching && <Loader />}
          {hasMoreAdverts && !isLastPage && (
            <LoadMoreBtn
              onClick={handleLoadMore}
              disabled={isFetching}
              text="Load more"
            />
          )}
        </>
      )}
    </>
  );
};

export default Catalog;
